.Nav{
    position: fixed;
    top: 50px; left: 30px;
    height: 70px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 
        0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.Nav>a{
    box-sizing: border-box;
    display: block;
    padding: 10px;
    height: 30px;
    color: indigo;
    font-weight: 600;
    text-decoration : none
}